'use client'

import { useState } from 'react'
import Link from 'next/link'
import Image from 'next/image'
import { Eye, EyeOff } from 'lucide-react'
import { authAPI } from '@/lib/api/auth'

export default function LoginPage() {
  const [formData, setFormData] = useState({
    emailOrPhone: '',
    password: ''
  })
  const [showPassword, setShowPassword] = useState(false)
  const [rememberMe, setRememberMe] = useState(false)
  const [errors, setErrors] = useState<{[key: string]: string}>({})

  const handleChange = (e: React.ChangeEvent<HTMLInputElement>) => {
    setFormData({
      ...formData,
      [e.target.name]: e.target.value
    })
    if (errors[e.target.name]) {
      setErrors({
        ...errors,
        [e.target.name]: ''
      })
    }
  }

// In app/login/page.tsx handleSubmit function:
const handleSubmit = async (e: React.FormEvent) => {
  e.preventDefault()

  try {
    const result = await authAPI.login({
      email_or_phone: formData.emailOrPhone,
      password: formData.password
    })
    alert('Login successful!')
    window.location.href = '/'
  } catch (error: unknown) {
  const errorMessage = error instanceof Error ? error.message : 'Login failed'
  console.error('Error details:', error)
  alert(errorMessage)
}
}

  const handleGoogleLogin = () => {
    // TODO: Implement Google OAuth
    console.log('Google login')
  }

  return (
    <div className="min-h-screen bg-black flex">
      {/* Left Side - Form (Mobile: Full width) */}
      <div className="w-full lg:w-1/2 flex items-center justify-center p-8">
        <div className="w-full max-w-md">
          {/* Logo */}
          <div className="mb-8 text-center">
            <Link href="/">
              <Image 
                src="/images/logo.png" 
                alt="Timora" 
                width={120} 
                height={40}
                className="h-10 w-auto mx-auto"
              />
            </Link>
          </div>

          {/* Mobile Toggle Tabs */}
          <div className="flex lg:hidden mb-6 bg-gray-900 rounded-lg p-1">
            <button 
              className="flex-1 text-center py-2 px-4 bg-white text-black rounded-md font-medium"
            >
              Login
            </button>
            <Link 
              href="/register"
              className="flex-1 text-center py-2 px-4 rounded-md text-gray-400 transition-colors"
            >
              Signup
            </Link>
          </div>

          {/* Headers */}
          <div className="mb-8 text-center lg:text-left">
            <h1 className="text-3xl md:text-4xl font-light text-white mb-2">
              Welcome Back!
            </h1>
            <p className="text-gray-400">
              Enter your credentials to access<br className="hidden lg:block" />
              your account and explore luxury
            </p>
          </div>

          {/* Form */}
          <form onSubmit={handleSubmit} className="space-y-4">
            <div>
              <input
                type="text"
                name="emailOrPhone"
                placeholder="Email Address or Phone Number"
                value={formData.emailOrPhone}
                onChange={handleChange}
                className="w-full px-4 py-3 bg-gray-900 border border-gray-800 rounded-lg text-white placeholder-gray-500 focus:outline-none focus:border-gray-600 transition-colors"
              />
              {errors.emailOrPhone && (
                <p className="text-red-500 text-sm mt-1">{errors.emailOrPhone}</p>
              )}
            </div>

            <div className="relative">
              <input
                type={showPassword ? 'text' : 'password'}
                name="password"
                placeholder="Password"
                value={formData.password}
                onChange={handleChange}
                className="w-full px-4 py-3 bg-gray-900 border border-gray-800 rounded-lg text-white placeholder-gray-500 focus:outline-none focus:border-gray-600 transition-colors pr-12"
              />
              <button
                type="button"
                onClick={() => setShowPassword(!showPassword)}
                className="absolute right-3 top-1/2 -translate-y-1/2 text-gray-400 hover:text-white"
              >
                {showPassword ? <EyeOff className="w-5 h-5" /> : <Eye className="w-5 h-5" />}
              </button>
              {errors.password && (
                <p className="text-red-500 text-sm mt-1">{errors.password}</p>
              )}
            </div>

            <div className="flex items-center justify-between">
              <div className="flex items-center">
                <input
                  type="checkbox"
                  id="remember"
                  checked={rememberMe}
                  onChange={(e) => setRememberMe(e.target.checked)}
                  className="w-4 h-4 bg-gray-900 border border-gray-800 rounded focus:ring-0"
                />
                <label htmlFor="remember" className="ml-2 text-sm text-gray-400">
                  Remember Me
                </label>
              </div>
              <Link href="/forgot-password" className="text-sm text-white hover:underline">
                Forgot Password?
              </Link>
            </div>

            <button
              type="submit"
              className="w-full py-3 bg-white text-black rounded-lg font-medium hover:bg-gray-200 transition-colors"
            >
              Sign In
            </button>
          </form>

          {/* Divider */}
          <div className="relative my-6">
            <div className="absolute inset-0 flex items-center">
              <div className="w-full border-t border-gray-800"></div>
            </div>
            <div className="relative flex justify-center text-sm">
              <span className="px-4 bg-black text-gray-400">OR</span>
            </div>
          </div>

          {/* Google Login */}
          <button 
            onClick={handleGoogleLogin}
            className="w-full py-3 bg-gray-900 border border-gray-800 rounded-lg text-white hover:bg-gray-800 transition-colors flex items-center justify-center gap-3"
          >
            <svg className="w-5 h-5" viewBox="0 0 24 24">
              <path fill="currentColor" d="M22.56 12.25c0-.78-.07-1.53-.2-2.25H12v4.26h5.92c-.26 1.37-1.04 2.53-2.21 3.31v2.77h3.57c2.08-1.92 3.28-4.74 3.28-8.09z"/>
              <path fill="currentColor" d="M12 23c2.97 0 5.46-.98 7.28-2.66l-3.57-2.77c-.98.66-2.23 1.06-3.71 1.06-2.86 0-5.29-1.93-6.16-4.53H2.18v2.84C3.99 20.53 7.7 23 12 23z"/>
              <path fill="currentColor" d="M5.84 14.09c-.22-.66-.35-1.36-.35-2.09s.13-1.43.35-2.09V7.07H2.18C1.43 8.55 1 10.22 1 12s.43 3.45 1.18 4.93l2.85-2.22.81-.62z"/>
              <path fill="currentColor" d="M12 5.38c1.62 0 3.06.56 4.21 1.64l3.15-3.15C17.45 2.09 14.97 1 12 1 7.7 1 3.99 3.47 2.18 7.07l3.66 2.84c.87-2.6 3.3-4.53 6.16-4.53z"/>
            </svg>
            Sign in with Google
          </button>

          {/* Sign Up Link */}
          <p className="text-center text-gray-400 mt-6">
            New here?{' '}
            <Link href="/register" className="text-white hover:underline">
              Create an Account
            </Link>
          </p>
        </div>
      </div>

      {/* Right Side - Image (Desktop only) */}
      <div className="hidden lg:block lg:w-1/2 relative">
        <Image
          src="/images/auth-bg.jpg"
          alt="Luxury Watch"
          fill
          className="object-cover"
          priority
        />
        <div className="absolute inset-0 bg-black/30"></div>
      </div>
    </div>
  )
}
